.current {
    width: 50vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


.date {
    font-size: 1.2vw;
    color: #fff;
    font-weight: 500;
    text-align: center;
    text-shadow: 0 0 3px #fff, 0 0 5px #000000;
}

.cityName {
    font-size: 2.9vw;
    color: #fff;
    font-weight: 700;
    text-align: center;
    text-transform: capitalize;
    /* text-shadow: 2px 2px 15px rgba(150, 150, 150, 1); */
    /* padding-top: 1.2vw; */
    padding-bottom: 0.6vw;
    text-shadow: 0 0 3px #fff, 0 0 5px #000000;
}

.citynameBig {
    font-size: 2.5vw;
    color: #fff;
    font-weight: 700;
    text-align: center;
    text-transform: capitalize;
    /* text-shadow: 2px 2px 15px rgba(150, 150, 150, 1); */
    /* padding-top: 1.2vw; */
    padding-bottom: 0.6vw;
    text-shadow: 0 0 3px #fff, 0 0 5px #000000;
}

.main-current-holder {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1vw;
    width: 50vw;
    align-items: center;
}

.current-holder {
    display: flex;
    align-items: flex-end;
    gap: 4vw;
}


.main-holder {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* gap: 0.5vw; */
}
.main-temp {
    font-size: 4vw;
    font-weight: 700;
    color: #fff;
    text-shadow: 0 0 3px #fff, 0 0 5px #000000;
}

.feels-like {
    color: #fff;
    font-size: 1.2vw;
    text-shadow: 0 0 3px #fff, 0 0 5px #000000;
    font-weight: 500;
}

.icon-holder {
    display: flex;
    align-items: center;
    flex-direction: column;
    /* 0 */
}

.current-icon {
    width: 6.5vw;
}

.current-description {
    text-transform: lowercase;
    color: #fff;
    font-size: 1.2vw;
    text-shadow: 0 0 3px #fff, 0 0 5px #000000;
    font-weight: 500;
}

.sun-holder {
    display: flex;
    align-items: center;
    gap: 5vw;
    text-align: center;
}

.sun {
    display: flex;
    flex-direction: column;
}

.sun-holder span {
    color: #fff;
    font-size: 1vw;
    font-weight: 500;
    letter-spacing: 0.1vw;
    text-shadow: 0 0 3px #fff, 0 0 5px #000000;
}

.sun-icon {
    width: 5vw;
}

@media (max-width: 600px) {
    .current {
        width: 95% !important;
        justify-content: center;
        align-items: center;
    }
    .left{
        /* background-color: rebeccapurple; */
        width: 95% !important;
    }
    .head-holder {
        display: flex;
        /* background-color: red; */
        flex-direction: column;
        align-items: center;
        width: 60%;
    }
    .date {
        font-size: 2.5vw;
        font-weight: 300;
    }
    .cityName {
        font-size: 5vw;
        font-weight: 500;
    }
    .main-current-holder {
        width: 100%;
    }
    .current-holder {
        width: 100%;
        align-items: center;
        /* justify-content: center; */
        justify-content: space-evenly;
        padding-top: 5vw;
    }
    .main-holder {
        /* width: 50%; */
    }
    .icon-holder {
        justify-content: center;
        align-items: center;
    }
    .current-icon {
        width: 13vw;
    }
    .current-description {
        font-size: 2.5vw;
    }
    .main-temp {
        font-size: 9vw;
    }   
    .feels-like {
        font-size: 3vw;
    } 
    .sun-holder {
        display: none;
    }
}